import React, { Component } from 'react';
import UserListStyles from './styles/UserListStyles';
import Table from './styles/Table';
import Icon from '../elements/Icon';
import { users } from '../data';

class UserList extends Component {
  render() {
    return (
      <UserListStyles>
        <Table>
          <tbody>
            <tr className="header">
              <th className="user-name">Name</th>
              <th className="user-email">Contact Information</th>
              <th className="user-can-order">Access</th>
              <th className="admin-controls">
                <Icon name="editUser" />
                <Icon name="deleteUser" />
              </th>
            </tr>
            {users.map(user => {
              <tr className="row">
                <td class="user-name">
                  <div class="name">
                    {user.firstName}
                    {user.lastName && ` ${user.lastName}`}
                  </div>
                  <div class="user-company">{user.company}</div>
                </td>
              </tr>;
            })}
          </tbody>
        </Table>
      </UserListStyles>
    );
  }
}

export default UserList;
